body {
  color: #2e2f42;
  background: #f4f4fd;
  margin: 30px 0;
  font-family: Roboto, sans-serif;
}

h2, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.breed-select {
  width: 300px;
  height: 30px;
  text-align: center;
  margin: auto auto 30px;
  display: flex;
}

.breed-select:hover, .breed-select:focus {
  border-color: rgba(68, 107, 157, .15);
  box-shadow: 0 4px 4px rgba(68, 107, 157, .15);
}

.cat-info {
  background-color: #e4e4e7;
